a{
    width: 100%;
    height: auto;
    text-decoration: none;
    color: #5b727d;
    font-weight: bold;
    display: flex;
    align-items: center;
}
li{
    list-style: none;
}
input{
    outline: none;
}
button{
    border: none;
}
.actve{
    color: var(--activet);
    fill: #0095ff !important;
    font-weight: 700;
    display: flex;
    align-items: center;
}

.rade-color{
    color: #fa2869;
}

.movie-card-row{
    width: auto;
    height: auto;
    margin-left: 13rem;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-wrap: wrap;
}

.wrapper {
  width: 100%;
  overflow: hidden;
  padding-top: 56px;
  height: 24rem;
}
.photobanner {
  position: relative;
  height: 20rem;
  margin-bottom: 30px;
  display: flex;
  align-items: center;
  width: 100%;
}

.photobanner img {
    width: 28rem;
    height: 15rem;
    object-fit: cover;
    border-radius: 23px;
    box-shadow: 0px 0px 20px #00000052;
    margin: 0px 12px;
}

.photobanner {
  animation: bannermove 50s linear infinite alternate-reverse;
}

@keyframes bannermove {
  from {
    left: 0px;
  }
  to {
    left: -2700px;
  }
}


.card-view {
    background: var(--cardmov);
    box-shadow: 0px 6px 18px rgba(0, 0, 0, 0.1);
    width: 100%;
    max-width: 315px;
    margin: 20px;
    border-radius: 10px;
    display: inline-block;
    cursor: pointer;
}
.card-view a{
    display: block;
}
 
.card-header {
    padding: 0;
    margin: 0;
    height: 367px;
    width: 100%;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding-top: 12px;
}

.card-header img{
    height: 100%;
    border-radius: 10px;
    box-shadow: 0px 20px 32px #00000038;
}
 
.card-view:hover {
    -webkit-transform: scale(1.03);
    transform: scale(1.03);
    box-shadow: 0px 10px 25px rgba(0, 0, 0, 0.08);
}
 
.card-movie-content {
    padding: 18px 18px 24px 18px;
    margin: 0;
}
 
.card-movie-content-head,
.card-movie-info {
    display: table;
    width: 100%;
}
 
.card-movie-title {
    font-size: 22px;
    margin: 0;
    display: table-cell;
}
 
.ratings {
    width: 50px;
    height: 15px;
    background-size: contain;
    display: table-cell;
    text-align: right;
    position: relative;
    margin-top: 5px;
    font-weight: 600;
    flex-direction: row;
}

.ratings svg{
    position: absolute;
    right: 46px;
    fill: #42e981;
}
 
.ratings span {
    color: #2196F3;
}
 
.card-movie-info {
    margin-top: 1em;
}
 
.movie-running-time {
    display: table-cell;
    text-transform: uppercase;
    text-align: center;
}
 
.movie-running-time:first-of-type {
    text-align: left;
}
 
.movie-running-time:last-of-type {
    text-align: right;
}
 
.movie-running-time label {
    display: block;
    margin-bottom: .5em;
    font-size: 10px;
    font-weight: 900;
}
 
.movie-running-time span {
    font-weight: 700;
    font-size: 11px;
}
 
@media Running and (max-width: 500px) {
    .card-view {
        width: 95%;
        max-width: 95%;
        margin: 1em;
        display: block;
    }
    .container {
        padding: 0;
        margin: 0;
    }
}
