@media(max-width: 889px){
    .nav-left-bar{
        width: 0%;
        overflow: hidden !important;
    }
    .top-nav-bar .search-btn{
        left: 5rem !important;
    }
    .movie-card-row{
        margin-left: 0rem !important;
        justify-content: center !important;
    }
    .ratings svg{
        position: absolute !important;
        right: 53px !important;
    }
    .backgrod-short .main-short {
        width: 100%;
    }
    .backgrod-short .main-short img {
        border-radius: 23px;
        margin: 12px 0px;
        width: 100%;
    }
}
@media(min-width: 889px){
    .nav-left-bar{
        width: 0% !important;
        width: 0px !important;
        width: 0rem !important;
        width: 0vw !important;
    }
    .nav-left-bar .bural-left-nav{
        width: 0% !important;
    }
}
@media(max-width: 741px){
    .main-movie-frem{
        width: 24rem !important;
    }
    .main-movie-frem .image-move-themal {
        border-radius: 0px 0px 0px 7rem !important;
    }
}

@media(max-width:393px){
    .main-movie-frem {
        width: 22rem !important;
    }
}

@media(max-width: 579px){
    .search-body .search-input input {
        width: 19rem !important;
    }
}
